<template>
	<view class="container">
		<view class="action">
			<view class="tips">ChatGPT官方接口通道</view>
			<!-- <view class="switch">
				<u-switch activeColor="#26B3A0" :value="true"></u-switch>
			</view> -->
		</view>
		<view class="answer">
			<view class="ask">{{questionInfo.qa_data.question}}</view>
			<view class="content">
				<u-parse :content="answer"></u-parse>
			</view>
		</view>
		<view class="sheet">
			<view class="btn">
				<u-button iconColor="#ffffff" color="#26B3A0" icon="file-text" text="复制问题答案" @click="copyAnswer">
				</u-button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				questionInfo: {},
				answer: ''
			};
		},
		onLoad(options) {
			let qId = options.avg;
			this.getAnswer(qId);
		},
		methods: {
			getAnswer(id) {
				//已经登录，则调用后端接口数据
				this.$http.requestApi('GET', 'chatgpt/question/answer/detail', {
					avg: id
				}).then(res => {
					this.questionInfo = res.data;
					this.answer = res.data.qa_data.answer.replace(/[\n\r]/g, '<br>');
				})
			},
			copyAnswer() {
				uni.setClipboardData({
					data: this.questionInfo.qa_data.answer, //要被复制的内容
					success: () => { //复制成功的回调函数
						uni.showToast({ //提示
							title: "复制成功"
						})
					}
				});
			},
		}
	}
</script>

<style lang="scss">
	.answer {
		width: 90%;
		margin: 0 auto;
		margin-top: 200rpx;
		z-index: -1;
		padding-bottom: 300rpx;

		.ask {
			font-size: 36rpx;
			font-weight: bolder;
		}

		.content {
			// margin-top: 30rpx;
			color: #606266;
			white-space: pre-wrap;

			.mbottom10 {
				text-align: center;
				margin-top: 20rpx;
			}
		}
	}

	.action {
		width: 92%;
		position: fixed;
		bottom: 0rpx;
		top: 0rpx;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 80rpx;
		align-items: center;
		box-shadow: 10rpx 10rpx 10rpx #eee;
		padding: 30rpx;
		z-index: 999;
		background-color: #fff;
	}

	.sheet {
		width: 100%;
		position: fixed;
		bottom: 0rpx;
		left: 0rpx;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		height: calc(env(safe-area-inset-bottom) + 50px);
		height: calc(constant(safe-area-inset-bottom) + 50px);
		padding: 20rpx 0;
		background-color: #fff;
		box-shadow: -10rpx -10rpx 10rpx #eee;

		.btn {
			width: 90%;
			.u-button {
				height: 90rpx;
			}
		}
	}
</style>
